import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

import PRF from "../components/Professional";
import PER from "../components/Personal";
import FOO from "../components/Footer";
//import ABO from "../components/About.vue";
import MAR from '../components/Marketing.vue'

const routes = [
  { path: "/professional", component: PRF },
  { path: "/personal", component: PER },
  { path: "/marketing", component: MAR },
  //{ path: "/about", component: ABO },
  { path: "/footer", component: FOO },
  { path: "/", component: PER },
];

// 3. Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
const router = new VueRouter({
  routes, // short for `routes: routes`
});

export default router;
